import React,{ Component } from "react"

export default class Icon extends Component{
    render(){
        const icbc = {
            lianjie: "\ue6fe",
            weixin: "\ue620",
            vue: "\ue600",
            react: "\ue608",
            QQ: "\ue603",
            boshi: "\ue6bd"
        };
        const { type, size, color, bgc} = this.props;
        console.log(type);
        return(
            <i className="iconfont icon-style" style={{ fontSize: size + "px", color: color, backGroundColor: bgc, padding: "2px"}}>
                {icbc[type]}
                </i>
        )
    }
}


