import React,{ Component } from "react"
import './index.css'
import Common from "../Common";
import Works from '../Works'
import Introduction from '../Introduction'
// import Icon from '../Icon'

class Portfolios extends Component{
    render(){
        const items = [
            {
                title: "多人博客共享-Vue",
                sourceUrl: "https://github.com/HongTao-Huang/vue-blog-share",
                previewUrl: "https://hongtao-huang.github.io/vue-blog-share/",
                introduction: <Introduction
                    describe=
                    "本项目是一个多用户共享博客的平台，用户需要注册登陆才能创建博客。
                    用户创建博客时可以设置自己的博客是否显示在首页，首页的博客所有用户都可以查看。
                    用户可以查看其他用户的所有博客，<code>markdown</code>显示博客"
                    feature="
                    注册、登陆、<code>Vue-router</code>路由跳转、查看博客详情、<code>markdown</code>高亮显示、
                    主页博客列表、用户博客列表、分页器
                    "
                />,
                technologyStack: <div>
                    <code className="stack">Vue</code>
                    <code className="stack">VueRouter</code>
                    <code className="stack">Vuex</code>
                    <code className="stack">axios</code>
                    <code className="stack">elementUi</code>
                    <code className="stack">less</code>
                    <code className="stack">webpack</code>
                    <code className="stack">Grid</code>
                    <code className="stack">markedJs</code>
                </div>
            },
            {
                title: "cnode社区-Vue",
                sourceUrl: "https://github.com/HongTao-Huang/vue-cnode",
                previewUrl: "https://hongtao-huang.github.io/vue-cnode/",
                introduction: <Introduction
                    describe=
                        "本项目是仿知名<code>cnode</code>社区，调用社区开源API,利用接口数据，展示信息。
                    "
                    feature="
                    可以查看全部列表，精华列表，分享列表，问答列表，招聘列表。帖子详情，显示评论信息
                    <code>markdown</code>显示帖子， 显示用户详情页
                    "
                />,
                technologyStack: <div>
                    <code className="stack">Vue</code>
                    <code className="stack">VueRouter</code>
                    <code className="stack">axios</code>
                    <code className="stack">elementUi</code>
                    <code className="stack">sass</code>
                    <code className="stack">webpack</code>
                    <code className="stack">flex</code>
                </div>
            },
            {
                title: "QQ音乐-JavaScript",
                sourceUrl: "https://hongtao-huang.github.io/vue-cnode/",
                previewUrl: "https://hongtao-huang.github.io/QQ-music/",
                introduction: <Introduction
                    describe=
                        "本项目是仿网页端QQ音乐，使用原生js 实现， 使用手机端会比较美观。
                    "
                    feature="
                        显示主页，轮播展示最新音乐歌单，展示歌曲排行榜，搜索功能，播放功能（需要先搜索选中歌曲播放）。
                    "
                />
            },
            {
                title: "哆啦A梦-Css",
                sourceUrl: "https://github.com/HongTao-Huang/js-animation-Doraemon",
                previewUrl: "https://hongtao-huang.github.io/js-animation-Doraemon/",
                introduction: <Introduction
                    describe="lalala<code>javascript<code>"
                    feature="lalala<code>javascript<code>"
                />
            },
            {
                title: "Apple键盘导航-JavaScript",
                sourceUrl: "https://github.com/HongTao-Huang/myNav",
                previewUrl: "https://hongtao-huang.github.io/myNav/",
                introduction: <Introduction
                    describe="lalala<code>javascript<code>"
                    feature="lalala<code>javascript<code>"
                />
            },
            {
                title: "简易画板-Canvas",
                sourceUrl: "https://github.com/HongTao-Huang/canvas-demo",
                previewUrl: "https://hongtao-huang.github.io/canvas-demo",
                introduction: <Introduction
                    describe="lalala<code>javascript<code>"
                    feature="lalala<code>javascript<code>"
                />
            },
            {
                title: "网页便签-node-express",
                sourceUrl: "https://github.com/HongTao-Huang/node-express-notes",
                introduction: <Introduction
                    describe="lalala<code>javascript<code>"
                    feature="lalala<code>javascript<code>"
                />
            },
        ];
        return (
            <Common title={"作品"}>
                {
                    items.map( (item, index) =>
                        <Works
                            key={index}
                            title={item.title}
                            sourceCode={item.sourceUrl}
                            preview={item.previewUrl}
                            introduction={item.introduction}
                            technologyStack={item.technologyStack}
                        />
                    )
                }
            </Common>
        )
    }
}

export default Portfolios;


