import React, { Component } from 'react';
import './App.css';
import { AboutMe, Portfolios} from "./components";

class App extends Component {
  render() {
    return (
      <div className="App">
          <AboutMe />
          <Portfolios />
      </div>
    );
  }
}

export default App;



