import React,{ Component } from 'react'
import './index.css'
import imgurl from  "../../images/cat-min.png"
import Icon from "../Icon"

class Works extends Component{
    render(){
        const { title, sourceCode, preview, introduction, technologyStack} = this.props;
        let imgWidth = 40;
        let imgheigh = imgWidth *3 / 4;
        return (
            <div className="works">
                <div className="works-image" style={{ width: imgWidth+'%', height: imgheigh+'%'}}>
                    <div className="image-wrpper">
                        <img src={ imgurl } alt="图片" className="image-show"/>
                    </div>
                </div>
                <div className="works-content">
                    <div className="content-title">
                        <span className="title-tag">项目名</span>
                        {title}
                    </div>
                    <div className="works-url">
                        <div className="url-source">
                            <a href={sourceCode}>
                                <Icon type="lianjie"/>项目地址
                            </a>
                        </div>
                        <div className="url-preview">
                            <a href={preview}>
                                <Icon type="lianjie"/>预览链接
                            </a>
                        </div>
                    </div>
                    <div className="content-title">
                        <span className="title-tag">作品简介</span>
                        { introduction }
                    </div>
                    <div className="content-title" style={{ marginTop: "10px"}}>
                        <span className="title-tag">技术栈</span>
                        { technologyStack }
                    </div>
                </div>
            </div>
        )
    }
}

export default Works;


